import {
  USER_REQUEST_FAIL,
  USER_REQUEST_SUCCESS,
  USER_REQUEST_REQUEST,
  USER_HOUSEHOLD_FAIL,
  USER_HOUSEHOLD_SUCCESS,
  USER_HOUSEHOLD_REQUEST,
  USER_NYIF_FAIL,
  USER_NYIF_SUCCESS,
  USER_NYIF_REQUEST,
} from "../constants/userConstants";

export const userReducer = (state = {}, action) => {
  switch (action.type) {
    case USER_REQUEST_REQUEST:
      return { loading: true };
    case USER_REQUEST_SUCCESS:
      return { loading: false, info: action.payload, success: true };
    case USER_REQUEST_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};

