import React, { useState } from "react";
import { Link } from "react-router-dom";
import CurrencyFormat from "react-currency-format";
import Header from "../UI/Header";
import styles from "./styles.module.css";

const NonBeneficiary = (props) => {
  const [loan, setLoan] = useState("");
  const [years, setYears] = useState("");
  const interest = 0.09;
  const upfront = 0.04;
  const submitHandler = (e) => {
    e.preventDefault();
    // Calculate how many months
    const months = years * 12;
    // Remove Strings from loan amount
    const currency = Number(loan.replace(/[^0-9.-]+/g, ""));
    // Calculate Intrest
    const totalIntrest = currency * interest * years;
    // Calculate Upfront Charge
    const UpfrontCharge = currency * upfront;
    const TotalUpfront = totalIntrest - UpfrontCharge;
    // Calculate total Loan Amount
    const TotalAmount = currency + TotalUpfront;
    // Calculate Monthly Dues
    const MonthlyDue = TotalAmount / months;

    // Set items to localStorage
    localStorage.setItem("loan", loan);
    localStorage.setItem("years", years);
    localStorage.setItem("TotalIntrest", totalIntrest);
    localStorage.setItem("UpfrontCharge", UpfrontCharge);
    localStorage.setItem("TotalUpfront", TotalUpfront);
    localStorage.setItem("TotalAmount", TotalAmount);
    localStorage.setItem("MonthlyDue", MonthlyDue);
    // Redirect to another Page

    props.history.push("/non-beneficiary/results");
    window.location.reload();
  };
  return (
    <div className={styles.contents}>
      <Header contents="Enter Your Loan Information to Calculate your Loan" />
      <br />
      <br />
      <form onSubmit={submitHandler}>
        <div className="col-md-6 form-group">
          <label className="text-success">
            Loan Amount <small className="text-danger">[i.e 500,000]</small>
          </label>
          <CurrencyFormat
            thousandSeparator={true}
            prefix={"N"}
            name="loan"
            required
            onChange={(e) => setLoan(e.target.value)}
            className={`${styles.inputs}`}
          />
        </div>
        <br />
        <div className="col-md-6 form-group">
          <label className="text-success">
            Loan Term <small className="text-danger">[Number of Years]</small>
          </label>
          <input
            type="number"
            className={`${styles.inputs}`}
            name="years"
            onChange={(e) => setYears(e.target.value)}
          />
        </div>
        <br />
        <div className="col-md-6 form-group">
          <label />
          <input
            type="submit"
            className={`${styles.btns} ${styles.btnGreen}`}
            name="submit"
            value="Calculate Loan"
          />
        </div>
      </form>
      <div className="col-md-1">
        <Link to="/" className={`${styles.btns} ${styles.btnLight}`}>
          Go Back
        </Link>
      </div>
    </div>
  );
};

export default NonBeneficiary;
