import React from "react";
import { Link } from "react-router-dom";
import CurrencyFormat from "react-currency-format";
import Header from "../UI/Header";
import styles from "./styles.module.css";

// Get items to localStorage
const loan = localStorage.getItem("loan");
const totalIntrest = localStorage.getItem("TotalIntrest");
const UpfrontCharge = localStorage.getItem("UpfrontCharge");
const TotalAmount = localStorage.getItem("TotalAmount");
const MonthlyDue = localStorage.getItem("MonthlyDue");

const Results = () => {
  return (
    <div className={styles.contents}>
      <Header contents="Results" />
      <br />
      <br />
      <div className="col-md-8">
        <form>
          <div className="row">
            <div className={`col-md-6 ${styles.paddings}`}>
              <label className="text-success">Loan Amount</label>
              <CurrencyFormat
                thousandSeparator={true}
                prefix={"N"}
                name="loan"
                value={loan}
                readOnly
                className={`${styles.inputs}`}
              />
            </div>

            <div className={`col-md-6 ${styles.paddings}`}>
              <label className="text-success">Estimated Total Intrest </label>
              <CurrencyFormat
                thousandSeparator={true}
                prefix={"N"}
                name="loan"
                value={totalIntrest}
                readOnly
                className={`${styles.inputs}`}
              />
            </div>

            <div className={`col-md-6 ${styles.paddings}`}>
              <label className="text-success">4% Upfront Charge </label>
              <CurrencyFormat
                thousandSeparator={true}
                prefix={"N"}
                name="loan"
                value={UpfrontCharge}
                readOnly
                className={`${styles.inputs}`}
              />
            </div>

            <div className={`col-md-6 ${styles.paddings}`}>
              <label className="text-success">
                Estimated Total Repayment after Upfront Charge{" "}
              </label>
              <CurrencyFormat
                thousandSeparator={true}
                prefix={"N"}
                name="loan"
                value={TotalAmount}
                readOnly
                className={`${styles.inputs}`}
              />
            </div>

            <div className={`col-md-6 ${styles.paddings}`}>
              <label className="text-success">
                Estimated Monthly Amount Due
              </label>
              <CurrencyFormat
                thousandSeparator={true}
                prefix={"N"}
                name="loan"
                value={MonthlyDue}
                readOnly
                className={`${styles.inputs}`}
              />
            </div>
            <div className={`col-md-6 ${styles.paddings}`}>
              <label />
              <Link
                to="/non-beneficiary"
                className={`${styles.btns} ${styles.btnLight}`}
              >
                Go Back
              </Link>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Results;
