import React from "react";
import { Link } from "react-router-dom";
import Header from "../../UI/Header";
import styles from "../styles.module.css";

const Covid19 = () => {
  return (
    <div className={styles.contents}>
      <Header contents="COVID19 TCF" />
      <div className="col-md-6">
        <Link
          to="/loan-beneficiary/CBN-intervention/COVID19/SME"
          className={`${styles.btns} ${styles.btnGreen}`}
        >
          SME
        </Link>
      </div>
      <div className="col-md-6">
        <Link
          to="/loan-beneficiary/CBN-intervention/COVID19/Household"
          className={`${styles.btns} ${styles.btnGreen}`}
        >
          Household
        </Link>
      </div>
      <div className="col-md-1">
        <Link
          to="/loan-beneficiary/CBN-intervention"
          className={`${styles.btns} ${styles.btnLight}`}
        >
          Go Back
        </Link>
      </div>
    </div>
  );
};

export default Covid19;
