import React from "react";
import { Link } from "react-router-dom";
import Header from "../UI/Header";
import styles from "./styles.module.css";
const Beneficiary = () => {
  return (
    <div className={styles.contents}>
      <Header contents="Select Product type" />
      <div className="col-md-6">
        <Link
          to="/loan-beneficiary/CBN-intervention"
          className={`${styles.btns} ${styles.btnGreen}`}
        >
          CBN Intervention (COVID19 TCF, ABP, AGSMEIS and NYIF e.t.c)
        </Link>
      </div>
      <div className="col-md-6">
        <Link
          to="/loan-beneficiary/on-balance-sheet"
          className={`${styles.btns} ${styles.btnGreen}`}
        >
          On-balance sheet (SALAD, Petty Now Now, SME Plus, etc)
        </Link>
      </div>
      <div className="col-md-1">
        <Link to="/" className={`${styles.btns} ${styles.btnLight}`}>
          Go Back
        </Link>
      </div>
    </div>
  );
};

export default Beneficiary;
