import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import Header from "../../UI/Header";
import styles from "../styles.module.css";
import { household } from "../../../actions/userActions";
import Spinner from "react-spinner-material";

const Household = (props) => {
  const [bvn, setBVN] = useState("");

  const dispatch = useDispatch();
  const house = useSelector((state) => state.house);
  const { info, loading, error, success } = house;
  const redirect = props.location.search.split("=")[1];

  const submitHandler = (e) => {
    e.preventDefault();
    // Calculate how many months
    dispatch(household(bvn));
    if (success) {
      // Redirect to another Page
    }
  };
  useEffect(() => {
    if (success) {
      props.history.push("/loan-beneficiary/household/results");
      window.location.reload();
    }
  }, [props.history, success, info]);
  return (
    <div className={styles.contents}>
      <Header contents="COVID19 TCF [Household]" />
      <br />
      <br />
      {redirect ? (
        <p className="alert alert-warning">
          User Not Found or Loan not Approved
        </p>
      ) : (
        ""
      )}
      {loading ? (
        <Spinner
          size={120}
          spinnerColor={"#008d44"}
          spinnerWidth={2}
          visible={true}
        />
      ) : (
        ""
      )}
      {error ? (
        <p className="alert alert-danger">
          An Error Occured, Please Try Again!
        </p>
      ) : (
        ""
      )}
      <form onSubmit={submitHandler}>
        <div className="col-md-6 form-group">
          <label className="text-success">Enter Your BVN</label>
          <input
            type="number"
            className={`${styles.inputs}`}
            name="bvn"
            onChange={(e) => setBVN(e.target.value)}
          />
        </div>
        <br />
        <div className="col-md-6 form-group">
          <label />
          <input
            type="submit"
            className={`${styles.btns} ${styles.btnGreen}`}
            name="submit"
            value="Calculate Loan"
          />
        </div>
      </form>
      <div className="col-md-1">
        <Link
          to="/loan-beneficiary/CBN-intervention/COVID19"
          className={`${styles.btns} ${styles.btnLight}`}
        >
          Go Back
        </Link>
      </div>
    </div>
  );
};

export default Household;
