import React from "react";
import { Link } from "react-router-dom";
import Header from "../UI/Header";
import styles from "./styles.module.css";

const CBNIntervention = () => {
  return (
    <div className={styles.contents}>
      <Header contents="CBN Intervention" />
      <div className="col-md-6">
        <Link
          to="/loan-beneficiary/CBN-intervention/COVID19"
          className={`${styles.btns} ${styles.btnGreen}`}
        >
          COVID19
        </Link>
      </div>
      <div className="col-md-6">
        <Link
          to="/loan-beneficiary/CBN-intervention/TCF"
          className={`${styles.btns} ${styles.btnGreen}`}
        >
          TCF
        </Link>
      </div>
      <div className="col-md-6">
        <Link
          to="/loan-beneficiary/CBN-intervention/ABP"
          className={`${styles.btns} ${styles.btnGreen}`}
        >
          ABP
        </Link>
      </div>
      <div className="col-md-6">
        <Link
          to="/loan-beneficiary/CBN-intervention/AGSMEIS"
          className={`${styles.btns} ${styles.btnGreen}`}
        >
          AGSMEIS
        </Link>
      </div>
      <div className="col-md-6">
        <Link
          to="/loan-beneficiary/CBN-intervention/NYIF"
          className={`${styles.btns} ${styles.btnGreen}`}
        >
          NYIF
        </Link>
      </div>
      <div className="col-md-1">
        <Link
          to="/loan-beneficiary/"
          className={`${styles.btns} ${styles.btnLight}`}
        >
          Go Back
        </Link>
      </div>
    </div>
  );
};

export default CBNIntervention;
