import React from 'react'
import styles from './styles.module.css'
import logo from '../../assets/logo.png'
const Navbar = () => {
    return (
        <div className={styles.navbar}>
            <a href="https://nmfb.com.ng/">
                <img src={logo} alt="NMFB Logo" />
            </a>
        </div>
    )
}

export default Navbar
