import Axios from "axios";
import {
  USER_REQUEST_FAIL,
  USER_REQUEST_SUCCESS,
  USER_REQUEST_REQUEST,
  USER_HOUSEHOLD_REQUEST,
  USER_HOUSEHOLD_SUCCESS,
  USER_HOUSEHOLD_FAIL,
  USER_NYIF_REQUEST,
  USER_NYIF_SUCCESS,
  USER_NYIF_FAIL,
} from "../constants/userConstants";

export const sme = (bvn) => async (dispatch) => {
  dispatch({
    type: USER_REQUEST_REQUEST,
    payload: { bvn },
  });
  try {
    const { data } = await Axios.post(
      "http://localhost:8000/api/v1/sme/smesig",
      {
        bvn,
      }
    );
    dispatch({
      type: USER_REQUEST_SUCCESS,
      payload: data,
    });
    localStorage.setItem("info", JSON.stringify(data));
  } catch (error) {
    dispatch({
      type: USER_REQUEST_FAIL,
      payload: error.message,
    });
  }
};

