import React from "react";
import { Link } from "react-router-dom";
import Header from "../UI/Header";
import styles from "./styles.module.css";
const Home = () => {
  return (
    <div className={styles.contents}>
      <Header contents="Are you an existing NMFB Loan Beneficiary?" />
      <div className="row">
        <div className="col-md-3">
          <Link
            to="/loan-beneficiary"
            className={`${styles.btns} ${styles.btnGreen}`}
          >
            Yes
          </Link>
        </div>
        <div className="col-md-3">
          <Link
            to="/non-beneficiary"
            className={`${styles.btns} ${styles.btnDark}`}
          >
            No
          </Link>
        </div>
      </div>
    </div>
  );
};

export default Home;
