import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Navbar from "./components/Navbar";
import Beneficiary from "./components/screens/Beneficiary";
import CBNIntervention from "./components/screens/CBNIntervention";
import Covid19 from "./components/screens/Covid19";
import SME from "./components/screens/Covid19/SME";
import Household from "./components/screens/Covid19/Household";
import Home from "./components/screens/Home";
import LoanResults from "./components/screens/LoanResult";
import NonBeneficiary from "./components/screens/NonBeneficiary";
import Results from "./components/screens/Results";
import HouseholdResults from "./components/screens/Covid19/HouseholdResults";
import NYIF from "./components/screens/NYIF";
import NYIFResult from "./components/screens/NYIF/NYIFResult";

function App() {
  return (
    <>
      <Router>
        <Navbar />
        <Switch>
          <Route path="/" exact component={Home} />
          <Route path="/non-beneficiary" exact component={NonBeneficiary} />
          <Route path="/non-beneficiary/results" exact component={Results} />
          <Route path="/loan-beneficiary" exact component={Beneficiary} />
          <Route
            path="/loan-beneficiary/results"
            exact
            component={LoanResults}
          />
          <Route
            path="/loan-beneficiary/household/results"
            exact
            component={HouseholdResults}
          />
          <Route
            path="/loan-beneficiary/CBN-intervention"
            exact
            component={CBNIntervention}
          />
          <Route
            path="/loan-beneficiary/on-balance-sheet"
            exact
            component={CBNIntervention}
          />
          <Route
            path="/loan-beneficiary/CBN-intervention/COVID19"
            exact
            component={Covid19}
          />
          <Route
            path="/loan-beneficiary/CBN-intervention/COVID19/SME"
            exact
            component={SME}
          />
          <Route
            path="/loan-beneficiary/CBN-intervention/COVID19/Household"
            exact
            component={Household}
          />
          <Route
            path="/loan-beneficiary/CBN-intervention/NYIF"
            exact
            component={NYIF}
          />
          <Route
            path="/loan-beneficiary/CBN-intervention/NYIF/Results"
            exact
            component={NYIFResult}
          />
          {/* <Route component={Error} />    */}
        </Switch>
      </Router>
    </>
  );
}

export default App;
