import React from "react";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import CurrencyFormat from "react-currency-format";
import Header from "../../UI/Header";
import styles from "../styles.module.css";

// Get items to localStorage

const NYIFResult = (props) => {
  const nyifs = useSelector((state) => state.nyifs);
  const { info } = nyifs;
  if (!info.loan) {
    // Redirect to another Page
    props.history.push("/loan-beneficiary/CBN-intervention/NYIF?redirect=404");
    window.location.reload();
  }
  const interest = 0.09;
  const upfront = 0.04;
  const months = 36;
  const years = 3;
  const loan = JSON.stringify(info.loan);

  const currency = Number(loan.replace(/[^0-9.-]+/g, ""));
  // Calculate Intrest
  const totalIntrest = currency * interest * years;
  // Calculate Upfront Charge
  const UpfrontCharge = currency * upfront;
  const TotalUpfront = totalIntrest - UpfrontCharge;
  // Calculate total Loan Amount
  const TotalAmount = currency + TotalUpfront;
  // Calculate Monthly Dues
  const MonthlyDue = TotalAmount / months;
  return (
    <div className={styles.contents}>
      <Header contents="Household Results" />
      <br />
      <br />
      <h5>
        <b>Name:</b> <small>{info.name}</small>
      </h5>
      <h5>
        <b>Account Number</b> <small>{info.account ? info.account : ""}</small>
      </h5>
      <div className="col-md-8">
        <form>
          <div className="row">
            <div className={`col-md-6 ${styles.paddings}`}>
              <label className="text-success">Loan Amount</label>
              <CurrencyFormat
                thousandSeparator={true}
                prefix={"N"}
                name="loan"
                value={loan}
                readOnly
                className={`${styles.inputs}`}
              />
            </div>

            <div className={`col-md-6 ${styles.paddings}`}>
              <label className="text-success">Estimated Total Intrest </label>
              <CurrencyFormat
                thousandSeparator={true}
                prefix={"N"}
                name="loan"
                value={totalIntrest}
                readOnly
                className={`${styles.inputs}`}
              />
            </div>

            <div className={`col-md-6 ${styles.paddings}`}>
              <label className="text-success">4% Upfront Charge </label>
              <CurrencyFormat
                thousandSeparator={true}
                prefix={"N"}
                name="loan"
                value={UpfrontCharge}
                readOnly
                className={`${styles.inputs}`}
              />
            </div>

            <div className={`col-md-6 ${styles.paddings}`}>
              <label className="text-success">
                Estimated Total Repayment after Upfront Charge{" "}
              </label>
              <CurrencyFormat
                thousandSeparator={true}
                prefix={"N"}
                name="loan"
                value={TotalAmount}
                readOnly
                className={`${styles.inputs}`}
              />
            </div>

            <div className={`col-md-6 ${styles.paddings}`}>
              <label className="text-success">
                Estimated Monthly Amount Due
              </label>
              <CurrencyFormat
                thousandSeparator={true}
                prefix={"N"}
                name="loan"
                value={MonthlyDue}
                readOnly
                className={`${styles.inputs}`}
              />
            </div>
            <div className={`col-md-6 ${styles.paddings}`}>
              <label />
              <Link to="/" className={`${styles.btns} ${styles.btnLight}`}>
                Go Back
              </Link>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default NYIFResult;
